/* Base Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: var(--font-size-base);
  font-family: var(--font-family-base);
  line-height: var(--line-height-normal);
  color: var(--color-text);
  background-color: var(--color-background);
  -webkit-font-smoothing: antialiased;
}

body {
  margin: 0;
  padding: 0;
  transition: background-color var(--duration-normal) var(--ease-standard), color var(--duration-normal) var(--ease-standard);
}

/* Typography */
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: var(--font-weight-semibold);
  line-height: var(--line-height-tight);
  color: var(--color-text);
  letter-spacing: var(--letter-spacing-tight);
}

h1 {
  font-size: var(--font-size-4xl);
}

h2 {
  font-size: var(--font-size-3xl);
}

h3 {
  font-size: var(--font-size-2xl);
}

h4 {
  font-size: var(--font-size-xl);
}

h5 {
  font-size: var(--font-size-lg);
}

h6 {
  font-size: var(--font-size-md);
}

p {
  margin: 0 0 var(--space-16) 0;
}

a {
  color: var(--color-primary);
  text-decoration: none;
  transition: color var(--duration-fast) var(--ease-standard);
}

a:hover {
  color: var(--color-primary-hover);
}

code,
pre {
  font-family: var(--font-family-mono);
  font-size: calc(var(--font-size-base) * 0.95);
  background-color: var(--color-secondary);
  border-radius: var(--radius-sm);
}

code {
  padding: var(--space-1) var(--space-4);
}

pre {
  padding: var(--space-16);
  margin: var(--space-16) 0;
  overflow: auto;
  border: 1px solid var(--color-border);
}

pre code {
  background: none;
  padding: 0;
}

/* ===== Buttons ===== */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: var(--space-8) var(--space-16);
  border: none;
  border-radius: var(--radius-base);
  font-size: var(--font-size-base);
  font-weight: var(--font-weight-medium);
  line-height: 1.5;
  cursor: pointer;
  transition: all var(--duration-normal) var(--ease-standard);
  text-decoration: none;
  position: relative;
  gap: var(--space-8);
}

.btn:focus-visible {
  outline: none;
  box-shadow: var(--focus-ring);
}

.btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Primary Button */
.btn-primary,
.btn--primary {
  background-color: var(--color-primary);
  color: var(--color-btn-primary-text);
}

.btn-primary:hover:not(:disabled),
.btn--primary:hover:not(:disabled) {
  background-color: var(--color-primary-hover);
  transform: translateY(-1px);
  box-shadow: var(--shadow-md);
}

.btn-primary:active:not(:disabled),
.btn--primary:active:not(:disabled) {
  background-color: var(--color-primary-active);
}

/* Secondary Button */
.btn-secondary,
.btn--secondary {
  background-color: var(--color-secondary);
  color: var(--color-text);
}

.btn-secondary:hover:not(:disabled),
.btn--secondary:hover:not(:disabled) {
  background-color: var(--color-secondary-hover);
}

/* Danger Button */
.btn-danger {
  background-color: var(--color-error);
  color: var(--color-btn-primary-text);
}

.btn-danger:hover:not(:disabled) {
  background-color: rgba(var(--color-error-rgb), 0.8);
}

/* Success Button */
.btn-success {
  background-color: var(--color-success);
  color: var(--color-btn-primary-text);
}

/* Warning Button */
.btn-warning {
  background-color: var(--color-warning);
  color: var(--color-btn-primary-text);
}

/* Outline Button */
.btn--outline {
  background: transparent;
  border: 1px solid var(--color-border);
  color: var(--color-text);
}

.btn--outline:hover {
  background-color: var(--color-secondary);
}

/* Size Variants */
.btn-sm,
.btn--sm {
  padding: var(--space-4) var(--space-12);
  font-size: var(--font-size-sm);
  border-radius: var(--radius-sm);
}

.btn-lg,
.btn--lg {
  padding: var(--space-10) var(--space-20);
  font-size: var(--font-size-lg);
  border-radius: var(--radius-md);
}

.btn-block,
.btn--full-width {
  width: 100%;
  justify-content: center;
}

.btn-icon {
  padding: var(--space-8);
  border-radius: var(--radius-full);
  background-color: var(--color-secondary);
  border: none;
  cursor: pointer;
  transition: all var(--duration-normal) var(--ease-standard);
  font-size: var(--font-size-2xl);
}

.btn-icon:hover {
  background-color: var(--color-secondary-hover);
  transform: scale(1.1);
}

/* ===== Form Elements ===== */
.form-control {
  display: block;
  width: 100%;
  padding: var(--space-8) var(--space-12);
  font-size: var(--font-size-md);
  line-height: 1.5;
  color: var(--color-text);
  background-color: var(--color-surface);
  border: 1px solid var(--color-border);
  border-radius: var(--radius-base);
  transition: border-color var(--duration-fast) var(--ease-standard), box-shadow var(--duration-fast) var(--ease-standard);
}

textarea.form-control {
  font-family: var(--font-family-base);
  font-size: var(--font-size-base);
}

select.form-control {
  padding: var(--space-8) var(--space-12);
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-image: var(--select-caret-light);
  background-repeat: no-repeat;
  background-position: right var(--space-12) center;
  background-size: 16px;
  padding-right: var(--space-32);
}

@media (prefers-color-scheme: dark) {
  select.form-control {
    background-image: var(--select-caret-dark);
  }
}

[data-color-scheme="dark"] select.form-control {
  background-image: var(--select-caret-dark);
}

[data-color-scheme="light"] select.form-control {
  background-image: var(--select-caret-light);
}

.form-control:focus {
  border-color: var(--color-primary);
  outline: var(--focus-outline);
  box-shadow: var(--focus-ring);
}

.form-label {
  display: block;
  margin-bottom: var(--space-8);
  font-weight: var(--font-weight-medium);
  font-size: var(--font-size-sm);
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: var(--space-4);
  margin-bottom: var(--space-16);
}

.form-group label {
  font-weight: var(--font-weight-medium);
  color: var(--color-text);
  font-size: var(--font-size-base);
}

.form-group input,
.form-group textarea,
.form-group select,
.select-input {
  padding: var(--space-8) var(--space-12);
  border: 1px solid var(--color-border);
  border-radius: var(--radius-base);
  font-size: var(--font-size-base);
  background-color: var(--color-surface);
  color: var(--color-text);
  transition: all var(--duration-normal) var(--ease-standard);
}

.form-group input:focus,
.form-group textarea:focus,
.form-group select:focus,
.select-input:focus {
  outline: var(--focus-outline);
  border-color: var(--color-primary);
  box-shadow: var(--focus-ring);
}

.form {
  display: flex;
  flex-direction: column;
  gap: var(--space-16);
}

.form-actions {
  display: flex;
  gap: var(--space-8);
  justify-content: flex-end;
  margin-top: var(--space-16);
}

/* ===== Cards ===== */
.card {
  background-color: var(--color-surface);
  border-radius: var(--radius-lg);
  padding: var(--space-24);
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--color-card-border);
  transition: all var(--duration-normal) var(--ease-standard);
  overflow: hidden;
}

.card:hover {
  box-shadow: var(--shadow-md);
  transform: translateY(-2px);
}

.card.inactive {
  opacity: 0.6;
}

.card__body,
.card-body {
  padding: var(--space-16);
}

.card__header,
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--space-16);
}

.card__header h3,
.card-header h3 {
  font-size: var(--font-size-2xl);
  font-weight: var(--font-weight-bold);
  color: var(--color-text);
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.card-body p {
  margin-bottom: var(--space-8);
  display: -webkit-box;
  overflow: hidden;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  white-space: normal;
  text-overflow: ellipsis;
}

.card-actions {
  display: flex;
  justify-content: flex-end;
  gap: var(--space-8);
  margin-top: auto;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: var(--space-24);
  margin-top: var(--space-24);
}

/* ===== Navbar ===== */
.navbar {
  background-color: var(--color-surface);
  border-bottom: 1px solid var(--color-border);
  box-shadow: var(--shadow-sm);
  position: sticky;
  top: 0;
  z-index: 100;
}

.navbar-container {
  max-width: var(--container-xl);
  margin: 0 auto;
  padding: var(--space-16) var(--space-20);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-brand h1 {
  font-size: var(--font-size-xl);
  color: var(--color-primary);
  font-weight: var(--font-weight-bold);
}

.navbar-toggle {
  display: none;
  flex-direction: column;
  gap: var(--space-4);
  background: none;
  border: none;
  cursor: pointer;
  padding: var(--space-8);
}

.navbar-toggle span {
  display: block;
  width: 24px;
  height: 2px;
  background-color: var(--color-text);
  transition: all var(--duration-normal) var(--ease-standard);
}

.navbar-menu {
  display: flex;
  align-items: center;
  gap: var(--space-24);
}

.navbar-links {
  display: flex;
  gap: var(--space-16);
}

.nav-link {
  text-decoration: none;
  color: var(--color-text-secondary);
  font-weight: var(--font-weight-medium);
  padding: var(--space-8) var(--space-12);
  border-radius: var(--radius-sm);
  transition: all var(--duration-normal) var(--ease-standard);
}

.nav-link:hover,
.nav-link.active {
  color: var(--color-primary);
  background-color: var(--color-secondary);
}

.navbar-actions {
  display: flex;
  gap: var(--space-8);
  align-items: center;
}

/* ===== Layout ===== */
.app-layout {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.main-content {
  flex: 1;
  padding: var(--space-24);
}

.container {
  max-width: var(--container-xl);
  margin: 0 auto;
}

/* ===== Page Layout ===== */
.page-container {
  animation: fadeIn var(--duration-normal) var(--ease-standard);
}

.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--space-32);
  flex-wrap: wrap;
  gap: var(--space-16);
}

.page-header h1 {
  font-size: var(--font-size-4xl);
  font-weight: var(--font-weight-bold);
  color: var(--color-text);
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(var(--space-8));
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ===== Modal ===== */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: var(--space-24);
  animation: fadeIn var(--duration-normal) var(--ease-standard);
}

.modal-content {
  background-color: var(--color-surface);
  border-radius: var(--radius-lg);
  box-shadow: var(--shadow-lg);
  width: 100%;
  max-height: 90vh;
  overflow-y: auto;
  animation: slideUp var(--duration-normal) var(--ease-standard);
}

.modal-medium {
  max-width: var(--container-md);
}

.modal-large {
  max-width: var(--container-lg);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--space-24);
  border-bottom: 1px solid var(--color-border);
}

.modal-header h2 {
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-bold);
  color: var(--color-text);
}

.modal-close {
  background: none;
  border: none;
  font-size: var(--font-size-4xl);
  color: var(--color-text-secondary);
  cursor: pointer;
  padding: 0;
  width: var(--space-24);
  height: var(--space-24);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: var(--radius-full);
  transition: all var(--duration-normal) var(--ease-standard);
}

.modal-close:hover {
  background-color: var(--color-secondary);
  color: var(--color-text);
}

.modal-body {
  padding: var(--space-24);
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(var(--space-24));
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ===== Status Indicators ===== */
.status {
  display: inline-flex;
  align-items: center;
  padding: var(--space-6) var(--space-12);
  border-radius: var(--radius-full);
  font-weight: var(--font-weight-medium);
  font-size: var(--font-size-sm);
}

.status--success {
  background-color: rgba(var(--color-success-rgb), var(--status-bg-opacity));
  color: var(--color-success);
  border: 1px solid rgba(var(--color-success-rgb), var(--status-border-opacity));
}

.status--error {
  background-color: rgba(var(--color-error-rgb), var(--status-bg-opacity));
  color: var(--color-error);
  border: 1px solid rgba(var(--color-error-rgb), var(--status-border-opacity));
}

.status--warning {
  background-color: rgba(var(--color-warning-rgb), var(--status-bg-opacity));
  color: var(--color-warning);
  border: 1px solid rgba(var(--color-warning-rgb), var(--status-border-opacity));
}

.status--info {
  background-color: rgba(var(--color-info-rgb), var(--status-bg-opacity));
  color: var(--color-info);
  border: 1px solid rgba(var(--color-info-rgb), var(--status-border-opacity));
}

/* ===== Badges ===== */
.badge {
  display: inline-block;
  padding: var(--space-4) var(--space-8);
  border-radius: var(--radius-sm);
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-bold);
}

.badge-info {
  background-color: var(--color-info);
  color: var(--color-btn-primary-text);
}

.badge-warning {
  background-color: var(--color-warning);
  color: var(--color-btn-primary-text);
}

.badge-success {
  background-color: var(--color-success);
  color: var(--color-btn-primary-text);
}

/* ===== Messages ===== */
.error-message {
  padding: var(--space-16);
  background-color: rgba(var(--color-error-rgb), var(--status-bg-opacity));
  color: var(--color-error);
  border-radius: var(--radius-base);
  border-left: 4px solid var(--color-error);
  margin-bottom: var(--space-16);
}

.empty-state {
  text-align: center;
  padding: var(--space-32);
  color: var(--color-text-secondary);
}

.empty-state p {
  font-size: var(--font-size-lg);
}

/* ===== Loading ===== */
.loading-screen {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  font-size: var(--font-size-2xl);
  color: var(--color-text-secondary);
}

.loading-spinner {
  display: flex;
  justify-content: center;
  padding: var(--space-32);
}

.spinner {
  width: 40px;
  height: 40px;
  border: 4px solid var(--color-secondary);
  border-top-color: var(--color-primary);
  border-radius: var(--radius-full);
  animation: spin 0.8s linear infinite;
}

.loading-spinner-small .spinner {
  width: 24px;
  height: 24px;
  border-width: 3px;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* ===== Utilities ===== */
.text-muted {
  color: var(--color-text-secondary);
}

.text-secondary {
  color: var(--color-text-secondary);
}

.toggle-label {
  display: flex;
  align-items: center;
  gap: var(--space-8);
  cursor: pointer;
  user-select: none;
  margin-right: var(--space-16);
}

.toggle-label input[type="checkbox"] {
  cursor: pointer;
}

.flex {
  display: flex;
}

.flex-col {
  flex-direction: column;
}

.items-center {
  align-items: center;
}

.justify-center {
  justify-content: center;
}

.justify-between {
  justify-content: space-between;
}

.gap-4 {
  gap: var(--space-4);
}

.gap-8 {
  gap: var(--space-8);
}

.gap-16 {
  gap: var(--space-16);
}

.m-0 {
  margin: 0;
}

.mt-8 {
  margin-top: var(--space-8);
}

.mb-8 {
  margin-bottom: var(--space-8);
}

.mx-8 {
  margin-left: var(--space-8);
  margin-right: var(--space-8);
}

.my-8 {
  margin-top: var(--space-8);
  margin-bottom: var(--space-8);
}

.p-0 {
  padding: 0;
}

.py-8 {
  padding-top: var(--space-8);
  padding-bottom: var(--space-8);
}

.px-8 {
  padding-left: var(--space-8);
  padding-right: var(--space-8);
}

.py-16 {
  padding-top: var(--space-16);
  padding-bottom: var(--space-16);
}

.px-16 {
  padding-left: var(--space-16);
  padding-right: var(--space-16);
}

.block {
  display: block;
}

.hidden {
  display: none;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border-width: 0;
}

:focus-visible {
  outline: var(--focus-outline);
  outline-offset: 2px;
}

hr {
  border: none;
  border-top: 1px solid var(--color-border);
  margin: var(--space-24) 0;
}

/* ===== Search ===== */
.search-input {
  width: 100%;
  padding: var(--space-16) var(--space-20);
  border: 1px solid var(--color-border);
  border-radius: var(--radius-md);
  font-size: var(--font-size-lg);
  background-color: var(--color-surface);
  color: var(--color-text);
}

.search-bar {
  margin-bottom: var(--space-24);
}

/* ===== Section Header ===== */
.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--space-16);
}

/* ===== Login ===== */
.login-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, var(--color-primary) 0%, var(--color-slate-900) 100%);
  padding: var(--space-24);
}

.login-card {
  background-color: var(--color-surface);
  border-radius: var(--radius-lg);
  padding: var(--space-32);
  box-shadow: var(--shadow-lg);
  width: 100%;
  max-width: 400px;
  animation: slideUp 0.5s var(--ease-standard);
}

.login-header {
  text-align: center;
  margin-bottom: var(--space-32);
}

.login-header h1 {
  color: var(--color-primary);
  margin-bottom: var(--space-8);
}

.login-header p {
  color: var(--color-text-secondary);
}

.login-form {
  display: flex;
  flex-direction: column;
  gap: var(--space-24);
}

/* ===== Customer Detail ===== */
.customer-detail {
  display: flex;
  flex-direction: column;
  gap: var(--space-24);
}

.customer-info {
  background-color: var(--color-secondary);
  padding: var(--space-24);
  border-radius: var(--radius-md);
}

.info-row {
  display: flex;
  gap: var(--space-24);
  margin-bottom: var(--space-16);
  flex-wrap: wrap;
}

.info-row p {
  flex: 1;
  min-width: 200px;
}

.products-section h3 {
  margin-bottom: var(--space-16);
  color: var(--color-text);
}

.add-product-form {
  display: flex;
  gap: var(--space-8);
  margin-bottom: var(--space-24);
}

.add-product-form .select-input {
  flex: 1;
}

.product-list {
  display: flex;
  flex-direction: column;
  gap: var(--space-16);
  margin-top: var(--space-24);
}

.product-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--space-16);
  background-color: var(--color-secondary);
  border-radius: var(--radius-md);
  border-left: 4px solid var(--color-warning);
  gap: var(--space-16);
  flex-wrap: wrap;
}

.product-item.billed {
  border-left-color: var(--color-success);
  opacity: 0.8;
}

.product-info h4 {
  font-size: var(--font-size-lg);
  margin-bottom: var(--space-4);
  color: var(--color-text);
}

.product-price {
  font-weight: var(--font-weight-bold);
  color: var(--color-primary);
  margin-top: var(--space-4);
}

.product-actions {
  display: flex;
  gap: var(--space-8);
  align-items: center;
  flex-wrap: wrap;
}

.quantity-controls {
  display: flex;
  align-items: center;
  gap: var(--space-8);
  background-color: var(--color-surface);
  padding: var(--space-4);
  border-radius: var(--radius-sm);
}

.quantity {
  font-weight: var(--font-weight-bold);
  min-width: 30px;
  text-align: center;
  color: var(--color-text);
}

.total-price {
  text-align: right;
  padding: var(--space-24);
  background-color: var(--color-secondary);
  border-radius: var(--radius-md);
  margin-top: var(--space-16);
}

.total-price h3 {
  font-size: var(--font-size-xl);
  color: var(--color-primary);
}

/* ===== Category Section ===== */
.category-section {
  width: 100%;
  max-width: none;
  border-bottom: 1px solid var(--color-border);
  padding-bottom: var(--space-24);
  cursor: pointer;
  background-color: var(--color-surface);
  border-radius: var(--radius-lg);
  padding: var(--space-6);
  margin-bottom: var(--space-10);
}

.category-section h2 {
  font-size: var(--font-size-xl);
  margin-bottom: var(--space-16);
  color: var(--color-text);
  border-bottom: 2px solid var(--color-primary);
  padding-bottom: var(--space-8);
}

.category-header {
  cursor: pointer;
  display: flex;
  align-items: center;
  background: var(--color-secondary);
  padding: var(--space-8);
}

/* ===== Custom Select Dropdown ===== */
.custom-select-dropdown {
  position: relative;
  width: 300px;
}

.dropdown-set-open {
  border: 1px solid var(--color-border);
  border-radius: var(--radius-md);
  padding: var(--space-8) var(--space-12);
  cursor: pointer;
  background-color: var(--color-surface);
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: var(--color-text);
  font-size: var(--font-size-base);
  transition: all var(--duration-fast) var(--ease-standard);
}

.dropdown-set-open:hover {
  border-color: var(--color-primary);
  box-shadow: var(--focus-ring);
}

.select-list {
  border: 1px solid var(--color-border);
  border-radius: var(--radius-md);
  width: 100%;
  background-color: var(--color-surface);
  max-height: 300px;
  overflow-y: auto;
  position: absolute;
  top: 100%;
  left: 0;
  margin-top: var(--space-4);
  z-index: 999;
  box-shadow: var(--shadow-lg);
}

.user-defined-product {
  padding: var(--space-8) var(--space-12);
  cursor: pointer;
  border-bottom: 1px solid var(--color-border);
  font-weight: var(--font-weight-bold);
  color: var(--color-text);
  background-color: var(--color-surface);
  transition: background-color var(--duration-fast) var(--ease-standard);
}

.user-defined-product:hover,
.user-defined-product.active {
  background-color: var(--color-secondary);
}

.custom-product-inputs {
  padding: var(--space-16);
  background-color: var(--color-secondary);
  border-bottom: 1px solid var(--color-border);
}

.custom-product-inputs input[type="text"],
.custom-product-inputs input[type="number"] {
  width: 100%;
  padding: var(--space-12) var(--space-16);
  margin-bottom: var(--space-8);
  border: 1px solid var(--color-border);
  border-radius: var(--radius-md);
  background-color: var(--color-surface);
  font-size: var(--font-size-base);
  color: var(--color-text);
  transition: border-color var(--duration-fast) var(--ease-standard), box-shadow var(--duration-fast) var(--ease-standard);
}

.custom-product-inputs input[type="text"]::placeholder,
.custom-product-inputs input[type="number"]::placeholder {
  color: var(--color-text-secondary);
}

.custom-product-inputs input:focus {
  border-color: var(--color-primary);
  outline: var(--focus-outline);
  box-shadow: var(--focus-ring);
}

.custom-product-inputs .btn {
  margin-right: var(--space-8);
  min-width: 105px;
}

.custom-product-inputs .btn-primary {
  background: linear-gradient(90deg, var(--color-primary), var(--color-primary-hover));
  color: var(--color-btn-primary-text);
}

.custom-product-inputs .btn-secondary {
  background-color: var(--color-secondary);
  color: var(--color-text);
  border: 1px solid var(--color-border);
}

.custom-product-inputs .btn-secondary:hover:not(:disabled) {
  background-color: var(--color-secondary-hover);
}

/* Scrollbar styling */
.select-list::-webkit-scrollbar {
  width: var(--space-8);
}

.select-list::-webkit-scrollbar-track {
  background: transparent;
}

.select-list::-webkit-scrollbar-thumb {
  background-color: var(--color-border);
  border-radius: var(--space-4);
}

.select-list::-webkit-scrollbar-thumb:hover {
  background-color: var(--color-text-secondary);
}

/* ===== Responsive Design ===== */
@media (max-width: 768px) {
  .navbar-toggle {
    display: flex;
  }

  .navbar-menu {
    position: fixed;
    top: 73px;
    left: 0;
    right: 0;
    background-color: var(--color-surface);
    flex-direction: column;
    padding: var(--space-24);
    box-shadow: var(--shadow-lg);
    transform: translateX(-100%);
    transition: transform var(--duration-normal) var(--ease-standard);
    gap: var(--space-16);
  }

  .navbar-menu.active {
    transform: translateX(0);
  }

  .navbar-links {
    flex-direction: column;
    width: 100%;
  }

  .nav-link {
    width: 100%;
  }

  .navbar-actions {
    width: 100%;
    justify-content: space-between;
  }

  .page-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .page-header h1 {
    font-size: var(--font-size-xl);
  }

  .card-grid {
    grid-template-columns: 1fr;
  }

  .modal-content {
    max-width: 100%;
    margin: 0;
    border-radius: 0;
    max-height: 100vh;
  }

  .product-item {
    flex-direction: column;
    align-items: flex-start;
  }

  .product-actions {
    width: 100%;
  }

  .info-row {
    flex-direction: column;
    gap: var(--space-8);
  }

  .add-product-form {
    flex-direction: column;
  }

  .form-actions {
    flex-direction: column-reverse;
  }

  .form-actions button {
    width: 100%;
  }

  .custom-select-dropdown {
    width: 100%;
  }
}

@media (max-width: 480px) {
  .main-content {
    padding: var(--space-16);
  }

  .card {
    padding: var(--space-16);
  }

  .modal-body,
  .modal-header {
    padding: var(--space-16);
  }

  .custom-product-inputs {
    padding: var(--space-8) var(--space-16);
    border-radius: var(--radius-md);
  }

  .custom-product-inputs input {
    font-size: var(--font-size-base);
    padding: var(--space-8) var(--space-12);
  }

  .page-header h1 {
    font-size: var(--font-size-3xl);
  }

  .navbar-brand h1 {
    font-size: var(--font-size-lg);
  }

  .btn {
    padding: var(--space-8) var(--space-12);
    font-size: var(--font-size-sm);
  }

  .btn-sm {
    padding: var(--space-4) var(--space-8);
    font-size: var(--font-size-xs);
  }

  .form-group input,
  .form-group textarea,
  .form-group select,
  .select-input {
    padding: var(--space-8) var(--space-12);
    font-size: var(--font-size-base);
  }

  .navbar-container {
    padding: var(--space-16) var(--space-20);
  }

  .modal-header h2 {
    font-size: var(--font-size-lg);
  }

  .card-header h3 {
    font-size: var(--font-size-lg);
  }
}